<template>
    <div>
        <h3>날짜 및 시간 선택</h3>
        <input type="date" :value="dateValue" @input="updateDate" />
        <select :value="hourValue" @change="updateHour">
            <option v-for="hour in hours" :key="hour" :value="hour">
                {{ hour }}:00
            </option>
        </select>
    </div>
</template>

<script>
export default {
    name: "DateTimePickerComponent",
    props: {
        value: {
            type: String,
            required: false,
        },
    },
    computed: {
        dateValue() {
            return this.value ? this.value.split("T")[0] : "";
        },
        hourValue() {
            return this.value ? this.value.split("T")[1]?.split(":")[0] : "00";
        },
    },
    data() {
        return {
            hours: Array.from({ length: 24 }, (_, i) => String(i).padStart(2, "0")),
        };
    },
    methods: {
        updateDate(e) {
            const newDate = e.target.value;
            const time = `${this.hourValue}:00`;
            this.$emit("input", `${newDate}T${time}`);
        },
        updateHour(e) {
            const newHour = e.target.value;
            const date = this.dateValue || new Date().toISOString().split("T")[0];
            this.$emit("input", `${date}T${newHour}:00`);
        },
    },
};
</script>

<style>
h3 {
    margin-bottom: 10px;
}

input,
select {
    margin-top: 5px;
    padding: 5px;
}
</style>
